import fs from "fs";
import path from "path";
import BlogPostClient from "./BlogPostClient";
import { remark } from "remark";
import html from "remark-html";

type BlogPageProps = {
  params: { slug: string };
};

export default async function BlogPage({ params }: BlogPageProps) {
  const { slug } = params;

  // Path to Markdown file
  const filePath = path.join(process.cwd(), "app", "blog", "data", `${slug}.md`);

  let content = "";
  if (fs.existsSync(filePath)) {
    content = fs.readFileSync(filePath, "utf-8");
  } else {
    content = "# Content Not Found\nThe requested blog content does not exist.";
    console.warn(`Markdown file missing for slug: ${slug}`);
  }

  // Convert Markdown to HTML
  const processedContent = await remark().use(html).process(content);
  const contentHtml = processedContent.toString();

  // Generate readable title from slug
  const title = slug
    .split(/[-_]/)
    .map((word) => word.charAt(0).toUpperCase() + word.slice(1))
    .join(" ");

  return <BlogPostClient post={{ title, content: contentHtml }} />;
}

/**
 * Generate static params for all blog posts
 * Required for static export (`output: 'export'`)
 */
export async function generateStaticParams() {
  const dataDir = path.join(process.cwd(), "app", "blog", "data");

  if (!fs.existsSync(dataDir)) {
    console.warn("Blog data directory not found:", dataDir);
    return [];
  }

  const files = fs.readdirSync(dataDir).filter((file) => file.endsWith(".md"));

  // Return array of { slug } matching dynamic route
  return files.map((file) => ({
    slug: file.replace(/\.md$/, ""),
  }));
}
